.grid-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .grid-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .grid-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .grid-container {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.header:hover {
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  margin: 10px 0;
}

.logo__img {
  display: block;
}

.navigation__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__item {
  position: relative;
}

.navigation__item:not(:last-child) {
  margin-right: 20px;
}

.navigation__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.navigation__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  transition-duration: .3s;
  display: flex;
}

.card:hover {
  color: #00acdc;
  transform: scale(1.2);
}

.card__wrap {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-top: 32px;
}

.card__text {
  flex-direction: column;
}

.card__name-goods {
  color: #060b35;
  margin: 40px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__name-goods:hover {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 4px 0 0 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__reviews {
  justify-content: space-between;
  width: 100%;
  margin: 17px 0 22px;
  display: flex;
}

.card__number-reviews {
  color: #060b35;
  margin: 0 16px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-text {
  color: #616070;
  margin: 0 0 16px 16px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-numbr {
  color: #060b35;
  margin: 0 16px 16px 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  margin-left: 16px;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars__star:nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
}

